<template>
	<div class="wrapper">
		<ul>
			<li><img src="../../assets/images/0.jpg"></li>
			<li><img src="../../assets/images/1.jpg"></li>
			<li><img src="../../assets/images/2.jpg"></li>
			<li><img src="../../assets/images/3.jpg"></li>
			<li><img src="../../assets/images/4.jpg"></li>
			<li><img src="../../assets/images/0.jpg"></li>
		</ul>
		<div class="bottomprev">
			<div class="list active"></div>
			<div class="list"></div>
			<div class="list"></div>
			<div class="list"></div>
			<div class="list"></div>
		</div>
		<div class="left">
			<div>左</div>
		</div>
		<div class="right">
			<div>右</div>
		</div>
	</div>
</template>

<script>
	export default{
		mounted(){
			let wrapper = document.querySelector('.wrapper');
			let ul = document.querySelector('ul');
			let img = document.querySelector('img');
			let list = document.querySelectorAll('.list');
			let prev = document.querySelector('.left');
			let next = document.querySelector('.right');

			let index = 0;
			let timer = null;

			function move(offset) {
				ul.style.transition = '.5s';
				ul.style.left = -parseInt(offset) * index + 'px';
			}

			next.onclick = function() {
				if (index == list.length) {
					ul.style.transition = '0s';
					ul.style.left = '0px';
					index = 0
				}
				index ++;
				move(img.offsetWidth);
				classButton()
			}

			prev.onclick = function() {
				if (index == 0) {
					index = list.length;
					ul.style.transition = '0s';
					ul.style.left = -img.offsetWidth * index + 'px';
				}
				index--;
				move(img.offsetWidth);
				classButton()
			}

			function animate() {
				timer = setInterval(() => {
					setTimeout(() =>{
						next.onclick()
					},1000)
				},3000)
			}

			wrapper.onmouseover = function() {
				clearInterval(timer)
				clickButton()
			}
			wrapper.onmouseout = function() {
				animate()
			}

			function classButton() {
				for(let i = 0;i < list.length; i++){
					list[i].classList.remove('active')
				}
				if (index == list.length) {
					list[0].classList.add('active')
				}
				list[index].classList.add('active')
			}

			function clickButton() {
				for(let i = 0;i < list.length; i++){
					list[i].onclick = function() {
						index = i;
						move(img.offsetWidth);
						classButton()
					}
				}
			}

			clickButton()

		}
	}
</script>

<style lang="scss" scoped>
	.wrapper{
		width: 6rem;
		height: 3.38rem;
		overflow: hidden;	
		margin: 0 auto;
		position: relative;
		ul{
			display: flex;
			position:absolute;
			left: 0;
			li{
				img{
					width: 6rem;

				}
			}
		}
		.bottomprev{
			width: 100%;
			height: .08rem;
			position:absolute;
			display: flex;
			bottom:.16rem;
			justify-content: center;
			.list{
				width: .7rem;
				height: .08rem;
				display: inline-block;
				background: rgba(0,0,0,.6);
				margin: .06rem;
				&.active{
					background:white;
				}
			}
		}
		.left,.right{
			position:absolute;
			top: 50%;
			right: 0;
			div{
				width: .6rem;
				height: .6rem;
				background: white;
				line-height: .6rem;
				text-align: center;
			}
		}
		.left{
			left: 0;
		}
	}
</style>